	module.exports = {"data":{"site":{"siteMetadata":{"title":"Yanda's Lab","subtitle":"The existence of this blog fills you with determination.","author":{"name":"Yanda Huang"},"disqusShortname":"","url":"https://yanda.rocks"}},"markdownRemark":{"id":"/home/yanda/Development/yodahuang.github.io/src/pages/articles/2015-08-27---The-Story-of-a-White-Rabbit-and-a-Grey-Rabbit/index.md absPath of file >>> MarkdownRemark","html":"<h2>小白兔的故事</h2>\n<p>从前有一只可爱的小兔子 她有雪白的绒毛 大大的招风耳 笑起来的时候眼睛是眯眯眼 不过她总是不太开心 唔 为什么倪 ?原来她的尾巴不是一个小球球 而是一个长长的小尾巴~ 她觉得小家伙们一定会嘲笑她 于是总是喜欢把她的头埋在沙子里 “就知道你们看不到我 我要把自己藏起来~ ” 然后她的尾巴就在外面欢乐地摇来摇去 有一天 有一只大灰兔看到了那个奇怪的尾巴 就握着她的小尾巴把她拉了出来 只见小兔子顶着一头乱毛 好奇地看着他 她从来未曾看过如此可爱率真的小家伙 从来没有 只是她没有表现出来 于是 她又把自己的小脑袋埋进了沙子里 企图把自己的羞涩都藏起来 当大灰兔再一次把她抱出来的时候 月亮姐姐从云层里出来了 此刻 月光洒在两个小家伙的面容上 有种温馨的感触。。。</p>\n<h2>小灰兔的故事</h2>\n<p>从前有一只可爱的大灰兔，他非常非常喜欢自己。但是有那么多的大灰兔们，他觉得自己就像小家伙中的每一个人，但是他从来都不知道自己究竟和小家伙们有什么区别。直到有一天他在大草原上乱跑的时候，像往常一样找不到了回去的路，这时他看见一个奇怪的小尾巴在沙子上欢快地摇来摇去。“咦，世界上竟然会存在这么有意思的小家伙~”，这么想了一想，大灰兔就去继续拔他的胡萝卜了，毕竟三年以后大灰兔想用他积攒的胡萝卜换取一张胡萝卜乐园的入场券呢。但是不知道什么原因，大灰兔经常会看见这只小尾巴，偶尔能看到一只可爱的小兔子的小脑袋在沙子里若隐若现，好奇的小眼睛一眨一眨。“这分明就是一个很可爱的小兔子嘛”于是终于有一天，他忍不住拎起了那只小尾巴，不过还没有等大灰兔定睛看个清楚，小兔子就飞快地钻了回去。有一天，当大灰兔走出家门的时候，发现那只以前只出现在他梦里的小白兔的小脑袋出现在了他的家门口，比他以前想象的更可爱。不过大灰兔一直都没敢把小兔子从地里像拔萝卜那样地拔出来，他怕会伤害到那么与众不同又直率的小白兔。事情的转机，发生在另一只大黄兔绕着那只小尾巴滴溜溜打转的时候，当小灰兔走近那个小尾巴的时候，小尾巴跳了起来！然后 一只顶着乱毛的小兔子扑向了大灰兔的怀里。两个小家伙都觉得，没有更美好的事儿了。</p>\n<p>共写于2013年12月。</p>","fields":{"tagSlugs":["/tags/love/"],"date":"2015-08-27T23:55:35.000Z"},"frontmatter":{"title":"小白兔和大灰兔的故事","tags":["love"],"date":"2015-08-27T23:55:35.000Z","description":"A good love story written by me and my girl friend."}}},"pathContext":{"slug":"/articles/The-Story-of-a-White-Rabbit-and-a-Grey-Rabbit/"}}

